.phone-header p{
    margin: 0;
    font-weight: bold;
    font-size: 0.8rem;
}

.navbar{
    padding: 0!important;
}

.nav-logo {
    width: 110px;
    margin-right: 40px;
}

.navbar-nav a {
    font-size: 1rem;
    font-weight: bold;
}

.nav-link a {

}

.nav-link {
    padding: 10px 10px!important;
    color: #000!important;
}

.nav-link:hover {
    color: #ED1B24!important;
    transition: transform .5s;
    transform: scale(1.1);
}

.dropdown:hover>.dropdown-menu {
    display: block;
}

@media only screen and (min-width: 992px) {
    .cust-dropdown {
        left: 50%!important;
        transform: translateX(-50%);
    }
}

.cust-dropdown{
    border: none!important;
}

.dropdown-item {
    padding: .25rem 1rem!important;
}