.img-caption {
    text-transform: uppercase;
    font-weight: bold;
    font-size: 1.3em;
}

.prod {
    font-size: 2.5em;
}

.clien {
    font-size: 2.5em;
}

.prod-showcase {
    background-color: #000;
    border-radius: 5%;
}

.swiper-container {
    width: 100%;
    height: 100%;
}

.swiper-slide {
    /* padding-left: 1em;
    padding-right: 1em; */
    text-align: center;
    font-size: 18px;
    background: #fff;
    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
}
.swiper-button-prev, .swiper-button-next{
    color: #000!important;
}
.swiper-button-prev:hover, .swiper-button-next:hover{
    color: #ED1B24!important;
}
.swiper-slide .client-image {
    display: block;
    width: 60%;
    height: auto;
    object-fit: cover;
}

.card {
    border-radius: 15px!important;
    border: none!important;
}

.h5 {
    font-size: 3rem!important;
    font-weight: 400!important;
}

.card-img-top {
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.headng {
    font-weight: bold;
    font-size: 1.25rem;
}

.image-wrap {
    position: relative;
}

.desc-layer {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.8);
    border-radius: 10px;
    color: #fff;
    visibility: hidden;
    opacity: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: opacity .2s, visibility .2s;
}

.image-wrap:hover .desc-layer {
    visibility: visible;
    opacity: 1;
}

.desc-text {
    transition: .2s;
    transform: translateY(1em);
}

.image-wrap:hover .desc-text {
    transform: translateY(0);
}
.cust-btn{
    color: #fff!important;
    background-color: #000!important;
    border-color: #000!important;
}
.cust-btn:hover{
    color: #fff!important;
    background-color: #17a2b8!important;
    border-color: #17a2b8!important;
    transform: translateY(15%);
    transition: transform .5s;
}
.busi-image{
    width: 100%;
    height: auto;
