.cf-footer{
    background-color:#F9F9F9;
}

.footer-logo{
    width:40%;
}

.mail-link{
    text-decoration: none;
}
.mail-link:hover{
    text-decoration: none;
    color: #ED1B24;
    transition: transform .5s;
    transform: scale(1.2);
}

.tel-link{
    text-decoration: none;
}
.tel-link:hover{
    text-decoration: none;
    color: #ED1B24;
    transition: transform .5s;
    transform: scale(1.2);
}

.social-ico{
    font-size: 1.8em;
    padding: 0px 8px;
}

.footer-ul{
    list-style-type: none;
    padding: 0;
    margin: 0px;
}

.footer-ul li{
    padding: 6px 0px;
}

.footer-ul li a{
    text-decoration: none;
    color: #000;
}
.footer-ul li a:hover{
    color: #ED1B24;
}